@import "~antd/dist/antd.css";

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 50px;

}

.optionWrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}

.inputNumber {
  width: 200px;
}

.listItem {
  padding: 30px;
}

.item {
  display: flex;
}

.title {
  width: 100px;
}

.value {
  flex: 1;
  word-wrap: break-word;
  word-break: break-all;
}
